// --- Komponen Modal Kamera ---
const CameraModal = ({ onCapture, onCancel, setNotification }) => {
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const [stream, setStream] = useState(null);
  const [facingMode, setFacingMode] = useState('user'); // 'user' untuk kamera depan, 'environment' untuk kamera belakang

  useEffect(() => {
    const startCamera = async () => {
      try {
        const videoStream = await navigator.mediaDevices.getUserMedia({ video: { facingMode } });
        videoRef.current.srcObject = videoStream;
        setStream(videoStream);
      } catch (err) {
        console.error("Error accessing the camera:", err);
        setNotification({ type: 'error', message: 'Gagal mengakses kamera. Pastikan Anda memberikan izin.' });
        onCancel();
      }
    };
    startCamera();
    return () => {
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }
    };
  }, [facingMode]);

  const handleCapture = () => {
    const video = videoRef.current;
    const canvas = canvasRef.current;
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;
    canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
    const imageData = canvas.toDataURL('image/jpeg');
    onCapture(imageData);
  };

  const handleSwitchCamera = () => {
    setFacingMode(prevMode => (prevMode === 'user' ? 'environment' : 'user'));
  };

  return (
    <div className="fixed inset-0 z-50 bg-black bg-opacity-75 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg p-6 shadow-xl w-full max-w-lg space-y-4">
        <h3 className="text-xl font-bold text-center">Ambil Foto Selfie</h3>
        <div className="relative w-full h-auto rounded-lg overflow-hidden">
          <video ref={videoRef} autoPlay playsInline className="w-full h-full object-cover"></video>
          <canvas ref={canvasRef} style={{ display: 'none' }}></canvas>
        </div>
        <div className="flex justify-between gap-4">
          <button onClick={handleSwitchCamera} className="flex-1 py-2 px-4 bg-gray-200 rounded-lg text-gray-800 hover:bg-gray-300 transition-colors flex items-center justify-center gap-2">
            <RefreshCw size={18} /> Ganti Kamera
          </button>
          <button onClick={handleCapture} className="flex-1 py-2 px-4 bg-green-500 rounded-lg text-white hover:bg-green-600 transition-colors flex items-center justify-center gap-2">
            <Camera size={18} /> Ambil Foto
          </button>
        </div>
        <button onClick={onCancel} className="w-full py-2 px-4 bg-red-500 rounded-lg text-white hover:bg-red-600 transition-colors flex items-center justify-center gap-2">
            <X size={18} /> Batal
          </button>
      </div>
    </div>
  );
};


// --- Komponen Dasbor Siswa ---
const StudentDashboard = ({ user, supabase, settings, setNotification }) => {
  const [showCamera, setShowCamera] = useState(false);
  const [attendanceType, setAttendanceType] = useState('');
  const [todayAttendance, setTodayAttendance] = useState(null);
  const [loading, setLoading] = useState(true);

  const fetchAttendance = async () => {
    setLoading(true);
    const today = getLocalDateString();
    const { data, error } = await supabase
      .from('attendance')
      .select('*')
      .eq('user_nis', user.nis)
      .eq('date', today)
      .maybeSingle();
    
    if (error && error.code !== 'PGRST116') {
      console.error("Gagal mengambil data absensi siswa:", error);
      setNotification({ type: 'error', message: "Gagal memuat status absensi. Coba lagi nanti." });
    } else {
      setTodayAttendance(data);
    }
    setLoading(false);
  };

  useEffect(() => {
    if (!supabase) return;
    fetchAttendance();
  }, [user.nis, supabase]);

  const handleAttend = async (type) => {
    if (!settings) {
      setNotification({ type: 'error', message: "Pengaturan jam belum siap. Mohon tunggu sebentar." });
      return;
    }

    const now = new Date();
    const currentHour = now.getHours();

    if (type === 'in' && (currentHour < settings.checkin_start_hour || currentHour >= settings.checkin_end_hour)) {
      setNotification({ type: 'error', message: `Absen masuk hanya bisa dilakukan antara pukul ${String(settings.checkin_start_hour).padStart(2, '0')}:00 - ${String(settings.checkin_end_hour).padStart(2, '0')}:00.` });
      return;
    }

    if (type === 'out' && (currentHour < settings.checkout_start_hour || currentHour >= settings.checkout_end_hour)) {
      setNotification({ type: 'error', message: `Absen pulang hanya bisa dilakukan antara pukul ${String(settings.checkout_start_hour).padStart(2, '0')}:00 - ${String(settings.checkout_end_hour).padStart(2, '0')}:00.` });
      return;
    }
    
    setAttendanceType(type);
    setShowCamera(true);
  };

  const getAttendanceLocation = () => new Promise((resolve, reject) => {
    if (!navigator.geolocation) {
      reject(new Error("Geolocation is not supported by your browser."));
      return;
    }
    navigator.geolocation.getCurrentPosition(
      (position) => {
        resolve({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        });
      },
      (error) => {
        console.error("Error getting location:", error);
        reject(new Error("Gagal mendapatkan lokasi. Pastikan GPS aktif dan izin diberikan."));
      }
    );
  });

  const handleSelfieCapture = async (imageData) => {
    setShowCamera(false);
    setNotification({ type: 'info', message: 'Mengunggah foto dan menyimpan data...' });

    try {
      const dateStr = getLocalDateString();
      const fileExt = 'jpg';
      const filePath = `public/${user.nis}/${dateStr}-${attendanceType}.${fileExt}`;
      
      const response = await fetch(imageData);
      const blob = await response.blob();
      
      const { data: uploadData, error: uploadError } = await supabase.storage
        .from('selfies')
        .upload(filePath, blob, { upsert: true });
      if (uploadError) throw uploadError;

      const { data: urlData } = await supabase.storage
        .from('selfies')
        .getPublicUrl(filePath);
      const publicURL = urlData.publicUrl;

      const location = await getAttendanceLocation();
      
      let error = null;
      if (attendanceType === 'in') {
        const { error: insertError } = await supabase.from('attendance').insert({
          user_nis: user.nis,
          name: user.name,
          class: user.class,
          date: dateStr,
          time_in: new Date().toISOString(),
          selfie_in_url: publicURL,
          status: 'Hadir',
          latitude_in: location.latitude,
          longitude_in: location.longitude,
        });
        error = insertError;
      } else if (attendanceType === 'out' && todayAttendance) {
        const { error: updateError } = await supabase.from('attendance')
          .update({
            time_out: new Date().toISOString(),
            selfie_out_url: publicURL,
            latitude_out: location.latitude,
            longitude_out: location.longitude,
          })
          .eq('id', todayAttendance.id);
        error = updateError;
      }

      if (error) throw error;
      
      setNotification({ type: 'success', message: `Absen ${attendanceType === 'in' ? 'masuk' : 'pulang'} berhasil!` });
      fetchAttendance();

    } catch (error) {
      console.error("Error saving attendance: ", error);
      setNotification({ type: 'error', message: `Terjadi kesalahan: ${error.message}` });
    }
  };

  if (loading) return <div className="text-center p-8 text-gray-500">Memeriksa status absensi...</div>

  return (
    <div className="container mx-auto">
      {showCamera && <CameraModal onCapture={handleSelfieCapture} onCancel={() => setShowCamera(false)} setNotification={setNotification} />}
      
      <div className="bg-white p-6 rounded-xl shadow-lg">
        <h2 className="text-2xl font-bold text-gray-800 mb-4">Dasbor Siswa</h2>
        
        {!settings ? <div className="text-center p-4 bg-yellow-100 text-yellow-800 rounded-lg">Memuat pengaturan jam...</div> :
        <div className="grid md:grid-cols-2 gap-6">
          <div className={`p-6 rounded-lg text-center transition-all ${!todayAttendance || todayAttendance.status !== 'Hadir' ? 'bg-green-500 text-white' : 'bg-gray-200 text-gray-500'}`}>
            <h3 className="text-xl font-semibold mb-3">Absen Masuk ({String(settings.checkin_start_hour).padStart(2, '0')}:00 - {String(settings.checkin_end_hour).padStart(2, '0')}:00)</h3>
            <button onClick={() => handleAttend('in')} disabled={!!todayAttendance} className="w-full flex items-center justify-center gap-2 py-3 px-4 bg-white text-green-600 font-bold rounded-lg shadow-md hover:bg-green-50 disabled:bg-gray-300 disabled:text-gray-500 disabled:cursor-not-allowed"><LogIn size={20} /><span>Ambil Selfie Masuk</span></button>
            {todayAttendance && <p className="text-xs mt-2 text-gray-600">Anda sudah absen masuk hari ini.</p>}
          </div>
          <div className={`p-6 rounded-lg text-center transition-all ${todayAttendance && todayAttendance.status === 'Hadir' && !todayAttendance.time_out ? 'bg-red-500 text-white' : 'bg-gray-200 text-gray-500'}`}>
            <h3 className="text-xl font-semibold mb-3">Absen Pulang ({String(settings.checkout_start_hour).padStart(2, '0')}:00 - {String(settings.checkout_end_hour).padStart(2, '0')}:00)</h3>
            <button onClick={() => handleAttend('out')} disabled={!todayAttendance || !!todayAttendance.time_out} className="w-full flex items-center justify-center gap-2 py-3 px-4 bg-white text-red-600 font-bold rounded-lg shadow-md hover:bg-red-50 disabled:bg-gray-300 disabled:text-gray-500 disabled:cursor-not-allowed"><LogOut size={20} /><span>Ambil Selfie Pulang</span></button>
            {!todayAttendance && <p className="text-xs mt-2 text-gray-600">Anda harus absen masuk terlebih dahulu.</p>}
            {todayAttendance?.time_out && <p className="text-xs mt-2 text-gray-600">Anda sudah absen pulang hari ini.</p>}
          </div>
        </div>
        }

        {todayAttendance && (
          <div className="mt-8 p-4 bg-gray-50 rounded-lg">
            <h3 className="font-bold text-lg mb-2">Rekap Absensi Hari Ini</h3>
            <div className="flex flex-col md:flex-row md:items-center gap-4">
              {todayAttendance.selfie_in_url && (
                <div className="text-center">
                  <img src={todayAttendance.selfie_in_url} alt="Selfie Masuk" className="w-32 h-32 object-cover rounded-lg mx-auto shadow-sm" />
                  <p className="text-sm mt-1">Masuk: {new Date(todayAttendance.time_in).toLocaleTimeString('id-ID')}</p>
                  <p className="text-xs text-gray-500">
                    {todayAttendance.latitude_in && todayAttendance.longitude_in ? 
                      <a href={`https://www.google.com/maps?q=${todayAttendance.latitude_in},${todayAttendance.longitude_in}`} target="_blank" rel="noopener noreferrer" className="text-blue-500 hover:underline">
                        Lihat Lokasi
                      </a> : 'Lokasi tidak tersedia'
                    }
                  </p>
                </div>
              )}
              {todayAttendance.selfie_out_url && (
                <div className="text-center">
                  <img src={todayAttendance.selfie_out_url} alt="Selfie Pulang" className="w-32 h-32 object-cover rounded-lg mx-auto shadow-sm" />
                  <p className="text-sm mt-1">Pulang: {new Date(todayAttendance.time_out).toLocaleTimeString('id-ID')}</p>
                  <p className="text-xs text-gray-500">
                    {todayAttendance.latitude_out && todayAttendance.longitude_out ? 
                      <a href={`https://www.google.com/maps?q=${todayAttendance.latitude_out},${todayAttendance.longitude_out}`} target="_blank" rel="noopener noreferrer" className="text-blue-500 hover:underline">
                        Lihat Lokasi
                      </a> : 'Lokasi tidak tersedia'
                    }
                  </p>
                </div>
              )}
            </div>
          </div>
        )}
        <div className="text-center text-xs text-gray-400 mt-4">
          <p>Created by Abi Nijav</p>
        </div>
      </div>
    </div>
  );
};


// --- Komponen Modal Input Manual ---
const ManualAttendanceModal = ({ students, supabase, onClose, onSave, filterDate, setNotification }) => {
  const [selectedStudent, setSelectedStudent] = useState('');
  const [status, setStatus] = useState('Hadir');
  const [keterangan, setKeterangan] = useState('');

  const handleManualSave = async () => {
    if (!selectedStudent || !status) {
      setNotification({ type: 'error', message: 'Siswa dan Status tidak boleh kosong.' });
      return;
    }

    const studentData = students.find(s => s.nis === selectedStudent);
    if (!studentData) {
      setNotification({ type: 'error', message: 'Siswa tidak ditemukan.' });
      return;
    }

    try {
      // Cek apakah sudah ada absen hari ini
      const { data: existingAttendance, error: fetchError } = await supabase
        .from('attendance')
        .select('id')
        .eq('user_nis', selectedStudent)
        .eq('date', filterDate)
        .maybeSingle();

      if (fetchError && fetchError.code !== 'PGRST116') {
        throw fetchError;
      }

      if (existingAttendance) {
        // Update data yang sudah ada
        const { error: updateError } = await supabase
          .from('attendance')
          .update({
            status: status,
            keterangan: keterangan,
            time_in: status === 'Hadir' ? new Date().toISOString() : null,
            time_out: status === 'Hadir' ? new Date().toISOString() : null,
          })
          .eq('id', existingAttendance.id);
        if (updateError) throw updateError;
        setNotification({ type: 'success', message: 'Absensi manual berhasil diperbarui!' });
      } else {
        // Insert data baru
        const { error: insertError } = await supabase
          .from('attendance')
          .insert({
            user_nis: selectedStudent,
            name: studentData.name,
            class: studentData.class || null,
            date: filterDate,
            status: status,
            keterangan: keterangan,
          });
        if (insertError) throw insertError;
        setNotification({ type: 'success', message: 'Absensi manual berhasil ditambahkan!' });
      }

      onSave(); // Refresh laporan
      onClose();
    } catch (err) {
      setNotification({ type: 'error', message: `Gagal menyimpan absensi manual: ${err.message}` });
    }
  };

  return (
    <div className="fixed inset-0 z-50 bg-black bg-opacity-75 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg p-6 shadow-xl w-full max-w-lg space-y-4">
        <h3 className="text-xl font-bold text-center">Input Absensi Manual</h3>
        <div className="flex flex-col gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700">Tanggal</label>
            <input type="date" value={filterDate} disabled className="mt-1 block w-full px-3 py-2 border rounded-md bg-gray-100 cursor-not-allowed" />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700">Pilih Pengguna</label>
            <select
              value={selectedStudent}
              onChange={e => setSelectedStudent(e.target.value)}
              className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md"
            >
              <option value="">-- Pilih Siswa/Guru --</option>
              {students.map(s => (
                <option key={s.nis} value={s.nis}>
                  {s.name} ({s.nis}) - {s.role === 'student' ? `Kelas ${s.class}` : 'Guru'}
                </option>
              ))}
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700">Status</label>
            <select
              value={status}
              onChange={e => setStatus(e.target.value)}
              className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md"
            >
              <option value="Hadir">Hadir</option>
              <option value="Sakit">Sakit</option>
              <option value="Izin">Izin</option>
              <option value="Alpha">Alpha</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700">Keterangan (Opsional)</label>
            <textarea
              value={keterangan}
              onChange={e => setKeterangan(e.target.value)}
              className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md"
              rows="3"
            ></textarea>
          </div>
        </div>
        <div className="flex justify-end gap-4">
          <button onClick={onClose} className="py-2 px-4 bg-gray-200 rounded-lg text-gray-800 hover:bg-gray-300 transition-colors flex items-center gap-2">
            <X size={18} /> Batal
          </button>
          <button onClick={handleManualSave} className="py-2 px-4 bg-green-500 rounded-lg text-white hover:bg-green-600 transition-colors flex items-center gap-2">
            <Send size={18} /> Simpan
          </button>
        </div>
      </div>
    </div>
  );
};

// --- Komponen Modal Impor Massal (BARU) ---
const BulkImportModal = ({ supabase, onClose, onImportSuccess, setNotification }) => {
  const [file, setFile] = useState(null);
  const [isProcessing, setIsProcessing] = useState(false);

  // Load XLSX library dynamically
  useEffect(() => {
    if (!window.XLSX) {
      const script = document.createElement('script');
      script.src = 'https://cdnjs.cloudflare.com/ajax/libs/xlsx/0.18.5/xlsx.full.min.js';
      document.head.appendChild(script);
      return () => {
        document.head.removeChild(script);
      };
    }
  }, []);

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleImport = async () => {
    if (!file) {
      setNotification({ type: 'error', message: 'Silakan pilih file untuk diunggah.' });
      return;
    }

    if (!window.XLSX) {
      setNotification({ type: 'error', message: 'Pustaka Excel belum dimuat. Mohon tunggu sebentar.' });
      return;
    }

    setIsProcessing(true);
    setNotification({ type: 'info', message: 'Memproses file dan mengimpor data...' });

    const reader = new FileReader();
    reader.onload = async (e) => {
      try {
        const data = new Uint8Array(e.target.result);
        const workbook = window.XLSX.read(data, { type: 'array' });
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        const jsonData = window.XLSX.utils.sheet_to_json(worksheet);

        if (jsonData.length === 0) {
          throw new Error("File kosong atau tidak dapat dibaca.");
        }
        
        const requiredHeaders = ['NIS', 'Nama', 'Kelas'];
        const headers = Object.keys(jsonData[0] || {});
        const hasAllHeaders = requiredHeaders.every(h => headers.includes(h));
        
        if (!hasAllHeaders) {
          throw new Error(`File harus memiliki kolom: ${requiredHeaders.join(', ')}`);
        }

        const studentsToInsert = jsonData.map(row => ({
          nis: String(row.NIS),
          name: String(row.Nama),
          class: String(row.Kelas),
          role: 'student',
        }));

        const { error } = await supabase
          .from('users')
          .upsert(studentsToInsert, { onConflict: 'nis' }); // Menggunakan upsert untuk menghindari duplikasi

        if (error) {
          throw error;
        }

        setNotification({ type: 'success', message: `Berhasil mengimpor ${studentsToInsert.length} siswa!` });
        onImportSuccess();
        onClose();
      } catch (err) {
        console.error("Error importing students:", err);
        setNotification({ type: 'error', message: `Gagal mengimpor data: ${err.message}` });
      } finally {
        setIsProcessing(false);
      }
    };
    reader.onerror = (e) => {
      setIsProcessing(false);
      setNotification({ type: 'error', message: "Gagal membaca file." });
    };
    reader.readAsArrayBuffer(file);
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black bg-opacity-75">
      <div className="w-full max-w-lg p-6 space-y-4 bg-white rounded-lg shadow-xl">
        <h3 className="text-xl font-bold text-center">Impor Siswa Secara Kolektif</h3>
        <p className="text-sm text-gray-500">Unggah file Excel (.xlsx) atau CSV (.csv) dengan kolom `NIS`, `Nama`, dan `Kelas`.</p>
        <div className="flex flex-col items-center justify-center p-6 border-2 border-dashed rounded-lg border-gray-300 hover:border-blue-500 transition-colors">
          <UploadCloud size={32} className="text-gray-400 mb-2" />
          <input type="file" accept=".xlsx, .xls, .csv" onChange={handleFileChange} className="text-sm" />
          {file && <p className="mt-2 text-sm text-gray-600">File terpilih: **{file.name}**</p>}
        </div>
        <div className="flex justify-end gap-4">
          <button onClick={onClose} className="py-2 px-4 bg-gray-200 rounded-lg text-gray-800 hover:bg-gray-300 transition-colors flex items-center gap-2" disabled={isProcessing}>
            <X size={18} /> Batal
          </button>
          <button onClick={handleImport} className="py-2 px-4 bg-green-500 rounded-lg text-white hover:bg-green-600 transition-colors flex items-center gap-2" disabled={isProcessing}>
            <UploadCloud size={18} /> {isProcessing ? 'Mengunggah...' : 'Impor'}
          </button>
        </div>
      </div>
    </div>
  );
};


